import React, { useEffect, useState } from 'react';
import axios from 'axios'
import ListProduct from '../ListProduct'
import {handlePercentDiscount} from '../../../untils/index'
import { useDispatch } from 'react-redux';

function Xiaomi(props) {
    const dispatch = useDispatch()
    const [name] = useState('xiaomi');
    const [hotXiaomi, setHotXiaomi] = useState([])
    const [error, setError] = useState(null)
    const [loading, setLoading] = useState(true)

    useEffect(() => {
        let isMounted = true

        async function FetchApi(){
            try {
                setLoading(true)
                console.log('ang gi API...')
                const response = await axios.get(`/api/products/type/${name}`)

                if (!isMounted) return

                const responseData = response?.data

                console.log('API Response:', {
                    status: response.status,
                    statusText: response.statusText,
                    data: responseData
                })

                // Kim tra v x l d liu
                if (!responseData) {
                    throw new Error('Khng c d liu t API')
                }

                // m bo d liu l mt mng
                const products = Array.isArray(responseData) ? responseData : [responseData]

                // Kim tra tnh hp l ca tng sn phm
                const validProducts = products.filter(product => 
                    product && 
                    typeof product === 'object' &&
                    product.name &&
                    (typeof product.price === 'number' || !isNaN(Number(product.price))) &&
                    (typeof product.salePrice === 'number' || !isNaN(Number(product.salePrice)))
                )

                if (validProducts.length > 0) {
                    // Chuyn i gi thnh s nu cn
                    const normalizedProducts = validProducts.map(product => ({
                        ...product,
                        price: Number(product.price),
                        salePrice: Number(product.salePrice)
                    }))

                    setHotXiaomi(normalizedProducts)
                    setError(null)
                } else {
                    setError('Khng tm thy sn phm Xiaomi hp l.')
                    setHotXiaomi([])
                }
            } catch (error) {
                if (!isMounted) return
                
                console.error('Li khi ti d liu Xiaomi:', error)
                const errorMessage = error.response?.data?.message || error.message || 'C li xy ra khi ti d liu.'
                setError(errorMessage)
                setHotXiaomi([])
            } finally {
                if (isMounted) {
                    setLoading(false)
                }
            }
        }

        FetchApi()

        return () => {
            isMounted = false
        }
    }, [name])

    // X l tnh % gim gi
    const processedProducts = React.useMemo(() => {
        if (!Array.isArray(hotXiaomi) || hotXiaomi.length === 0) {
            return []
        }
        return handlePercentDiscount(hotXiaomi)
    }, [hotXiaomi])

    if (loading) {
        return (
            <section id="hotsale">
                <div className="hotsale">
                    <h2>Xiaomi</h2>
                    <div style={{ textAlign: 'center', padding: '20px' }}>
                        ang ti d liu...
                    </div>
                </div>
            </section>
        )
    }

    return (
        <section id="hotsale">
            <div className="hotsale">
                <h2>Xiaomi</h2>
                {error && (
                    <div style={{ color: 'red', textAlign: 'center', padding: '20px' }}>
                        {error}
                    </div>
                )}
                {processedProducts.length > 0 && (
                    <ListProduct HotSaleProducts={processedProducts} />
                )}
            </div>
        </section>
    );
}

export default Xiaomi;