import axios from 'axios'
import { BASE_URL } from '../constants/UserConstant'

export const login = (user) => async (dispatch) => {
    try {
      console.log('Attempting login with:', user);
      const config = {
        headers: {
          'Content-Type': 'application/json'
        }
      };
      console.log('Making API call to:', `${BASE_URL}/api/users/login`);
      const {data} = await axios.post(`${BASE_URL}/api/users/login`, user, config);
      console.log('Login response:', data);
      
      dispatch({ type: 'USER_LOGIN_SUCCESS', payload: data });
      localStorage.setItem('userInfo', JSON.stringify(data));
    } catch (error) {
      console.error('Login error:', error.response?.data || error.message);
      dispatch({ 
        type: 'USER_LOGIN_FAIL', 
        payload: error.response && error.response.data.message 
          ? error.response.data.message 
          : error.message 
      });
    }
};


export const SignupUser = (user) => async (dispatch) => {
    try {
      dispatch({ type: 'USER_REGISTER_REQUEST' });
      
      const config = {
        headers: {
          'Content-Type': 'application/json'
        }
      };
      
      const {data} = await axios.post(`${BASE_URL}/api/users/register`, user, config);
      
      dispatch({ type: 'USER_REGISTER_SUCCESS', payload: data });
      dispatch({ type: 'USER_LOGIN_SUCCESS', payload: data }); // Auto login after register
      localStorage.setItem('userInfo', JSON.stringify(data));
      document.location.href = '/';
    } catch (error) {
      dispatch({ 
        type: 'USER_REGISTER_FAIL',
        payload: error.response && error.response.data.message
          ? error.response.data.message 
          : error.message 
      });
    }
};

export const SignoutUser = (user) => async (dispatch) => {
  localStorage.removeItem('userInfo')
  dispatch({type: 'USER_SIGNOUT_SUCCESS', payload: {} })
  document.location.href = '/';
};

export const getAllUser = () => async (dispatch) => {
  try {
    const { data } = await axios.get(`${BASE_URL}/api/users/`)
    dispatch({ type: 'GET_ALL_USER', payload: data })
  } catch (error) {
    console.log(error.response.data.message)
  }
}

export const deleteUser = (userId) => async (dispatch) => {
  try {
    const { data } = await axios.delete(`${BASE_URL}/api/users/${userId}`)
    dispatch({ type: 'DELETE_USER', payload: data })
  } catch (error) {
    console.log(error.response.data.message)
  }
}