import React from 'react';
import { useDispatch } from 'react-redux';
import {AddToCart} from '../../actions/CartAction'
import {Link} from 'react-router-dom'
import {formatPrice} from '../../untils/index'

function DetailInfo(props) {
    const dispatch = useDispatch()
    const { product } = props;
    
    function handleAddProduct(product) {
        const action = AddToCart(product)
        dispatch(action)
    }

    return (
        <div className="detail-info-right">
            <div className="detail-info-right-price">
                <p className="price-box">
                    <span className="saleprice">{formatPrice(product.salePrice)}</span>
                    <span className="old-price">Gi nim yt : <strong className="price">{formatPrice(product.price)}</strong> </span>
                </p>
                <p className="detail-info-sale">
                    Sn phm thuc chng trnh HOT SALE CUI TUN - Nhanh tay thanh ton!
                            </p>
            </div>

            <div className="detail-info-right-buy">
                <div className="detail-info-right-buy-now">
                    <Link to="/cart" onClick={() => handleAddProduct(product)}>
                        <strong>MUA NGAY</strong>
                        <br></br>
                        <span>(Giao tn ni hoc ly ti ca hng)</span>
                    </Link>
                </div>
                <div className="detail-info-right-buy-installment">
                    <a href="">
                        <strong>TR GP 0%</strong>
                        <br></br>
                        <span>(Xt duyt qua in thoi)</span>
                    </a>
                    <a href="">
                        <strong>TR GP QUA TH</strong>
                        <br></br>
                        <span>(Visa, Master, JCB)</span>
                    </a>
                </div>
            </div>
        </div>
    );
}

export default DetailInfo;